@font-face {
  font-family: 'Pixeled';
  src: url('../fonts/Pixeled.ttf') format('truetype');
}

:root {
  --glitch-height: 100vh;
  --gap-horizontal: 10px;
  --gap-vertical: 5px;
  --time-anim: 4s;
  --delay-anim: 1s;
  --blend-mode-1: none;
  --blend-mode-2: none;
  --blend-mode-3: none;
  --blend-mode-4: none;
  --blend-mode-5: overlay;
  --blend-color-1: transparent;
  --blend-color-2: transparent;
  --blend-color-3: transparent;
  --blend-color-4: transparent;
  --blend-color-5: #af4949;
}

.container {
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;

  @media (min-width: 576px) {
    max-width:540px;
  }
  @media (min-width: 768px) {
    max-width:720px;
  }
  
  @media (min-width: 992px) {
    max-width:960px;
  }
  
  @media (min-width: 1200px) {
    max-width:1140px;
  }
  
}

.mt-0 { margin-top: 0; }
.mb-0 { margin-bottom: 0; }

// Button

.btn__glitch {
  display: flex;
  align-items: center;
  justify-content: center;
  line-height: 62px;
  background-color: white;
  color: black;
  box-shadow: 0 4px black;
  font-size: inherit;
  font-family: 'Pixeled', 'Press Start 2P', cursive;
  text-align: center;
  text-decoration: none;
  -webkit-transition: all 0.2s cubic-bezier(0.25, 0.46, 0.45, 0.94);
  -moz-transition: all 0.2s cubic-bezier(0.25, 0.46, 0.45, 0.94);
  -o-transition: all 0.2s cubic-bezier(0.25, 0.46, 0.45, 0.94);
  -ms-transition: all 0.2s cubic-bezier(0.25, 0.46, 0.45, 0.94);
  transition: all 0.2s cubic-bezier(0.25, 0.46, 0.45, 0.94);
  transform: translateZ(0);
  overflow: visible;

  &:hover {
    color: #a9a9a9;

    &> .icon {
      opacity: .3;
    }
  }

  &.btn__glitch--active {
    -webkit-filter: url("#filter");
    filter: url("#filter");
  
    &::after, 
    &::before {
      content: "";
      width: 1px;
      position: absolute;
      top: -1px;
      bottom: -1px;
    }
  
    &::after {
      left: -2px;
      background-color: #ff536b;
      animation: colorshift-red 2.6s infinite;
      animation-timing-function: step-end;
    }
  
    &::before {
      right: -2px;
      background-color: #3ffeff;
      animation: colorshift-blue 2.6s infinite;
      animation-timing-function: step-end;
    }
  }
}

.btn__glitch-icon {
  width: 40px;
  height: 40px;
  background: url('../images/alien-icon.svg') no-repeat center;
  margin-left: 18px;
}


@media (min-width: 1200px) {
  .btn__glitch {
    flex-direction: row;
    width: 336px;
    height: 62px;
  }
}

@keyframes colorshift-red {
  0%, 7% {
    background-color: #ff536b;
  }
  8%, 18% {
    background-color: transparent;
  }
  19% {
    background-color: #ff536b;
    width: 2px;
  }
  23%, 100% {
    background-color: transparent;
  }
}
@keyframes colorshift-blue {
  0%, 7% {
    background-color: #3ffeff;
  }
  8%, 18% {
    background-color: transparent;
  }
  19% {
    background-color: #3ffeff;
    width: 2px;
  }
  23%, 100% {
    background-color: transparent;
  }
}

// BG Glitch

.bg__glitch {
  background: #894F8C url("../images/intro-bg.png") repeat center;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  position: absolute;
  z-index: 1;

  &:nth-child(n+2) {
    opacity: 0;
    animation-duration: var(--time-anim);
    animation-delay: var(--delay-anim);
    animation-timing-function: linear;
    animation-iteration-count: infinite;
  }
  &:nth-child(2) {
    background-color: var(--blend-color-2);
    background-blend-mode: var(--blend-mode-2);
    animation-name: glitch-anim-2;
  }
  &:nth-child(2) {
    background-color: var(--blend-color-3);
    background-blend-mode: var(--blend-mode-3);
    animation-name: glitch-anim-3;
  }
  &:nth-child(4) {
    background-color: var(--blend-color-4);
    background-blend-mode: var(--blend-mode-4);
    animation-name: glitch-anim-4;
  }
  &:nth-child(5) {
    background-color: var(--blend-color-5);
    background-blend-mode: var(--blend-mode-5);
    animation-name: glitch-anim-flash;
  }
}

@keyframes glitch-anim-flash {
  0%, 5% {
      opacity: 0.2;
      transform: translate3d(var(--glitch-horizontal), var(--glitch-height), 0);
  }
  5.5%, 100% {
      opacity: 0;
      transform: translate3d(0, 0, 0);
  }
}

@keyframes glitch-anim {
  0% { clip: rect(55px, 9999px, 79px, 0); }
  5% { clip: rect(22px, 9999px, 155px, 0); }
  10% { clip: rect(117px, 9999px, 34px, 0); }
  15% {clip: rect(147px, 9999px, 58px, 0); }
  20% { clip: rect(142px, 9999px, 130px, 0); }
  25% { clip: rect(37px, 9999px, 71px, 0); }
  30% { clip: rect(152px, 9999px, 61px, 0); }
  35% { clip: rect(87px, 9999px, 80px, 0); }
  40% { clip: rect(111px, 9999px, 23px, 0); }
  45% { clip: rect(91px, 9999px, 43px, 0); }
  50% { clip: rect(21px, 9999px, 24px, 0); }
  55% { clip: rect(34px, 9999px, 98px, 0); }
  60% { clip: rect(96px, 9999px, 154px, 0); }
  65% { clip: rect(38px, 9999px, 81px, 0); }
  70% { clip: rect(55px, 9999px, 76px, 0); }
  75% { clip: rect(53px, 9999px, 103px, 0); }
  80% { clip: rect(64px, 9999px, 69px, 0); }
  85% { clip: rect(59px, 9999px, 73px, 0); }
  90% { clip: rect(130px, 9999px, 44px, 0); }
  95% { clip: rect(55px, 9999px, 130px, 0); }
  100% { clip: rect(159px, 9999px, 10px, 0); }
}

@keyframes glitch-anim-2 {
  0% {
      opacity: 1;
      transform: translate3d(var(--gap-horizontal), 0, 0);
      -webkit-clip-path: polygon(0 2%, 100% 2%, 100% 5%, 0 5%);
      clip-path: polygon(0 2%, 100% 2%, 100% 5%, 0 5%);
  }
  2% {
      -webkit-clip-path: polygon(0 15%, 100% 15%, 100% 15%, 0 15%);
      clip-path: polygon(0 15%, 100% 15%, 100% 15%, 0 15%);
  }
  4% {
      -webkit-clip-path: polygon(0 10%, 100% 10%, 100% 20%, 0 20%);
      clip-path: polygon(0 10%, 100% 10%, 100% 20%, 0 20%);
  }
  6% {
      -webkit-clip-path: polygon(0 1%, 100% 1%, 100% 2%, 0 2%);
      clip-path: polygon(0 1%, 100% 1%, 100% 2%, 0 2%);
  }
  8% {
      -webkit-clip-path: polygon(0 33%, 100% 33%, 100% 33%, 0 33%);
      clip-path: polygon(0 33%, 100% 33%, 100% 33%, 0 33%);
  }
  10% {
      -webkit-clip-path: polygon(0 44%, 100% 44%, 100% 44%, 0 44%);
      clip-path: polygon(0 44%, 100% 44%, 100% 44%, 0 44%);
  }
  12% {
      -webkit-clip-path: polygon(0 50%, 100% 50%, 100% 20%, 0 20%);
      clip-path: polygon(0 50%, 100% 50%, 100% 20%, 0 20%);
  }
  14% {
      -webkit-clip-path: polygon(0 70%, 100% 70%, 100% 70%, 0 70%);
      clip-path: polygon(0 70%, 100% 70%, 100% 70%, 0 70%);
  }
  16% {
      -webkit-clip-path: polygon(0 80%, 100% 80%, 100% 80%, 0 80%);
      clip-path: polygon(0 80%, 100% 80%, 100% 80%, 0 80%);
  }
  18% {
      -webkit-clip-path: polygon(0 50%, 100% 50%, 100% 55%, 0 55%);
      clip-path: polygon(0 50%, 100% 50%, 100% 55%, 0 55%);
  }
  20% {
      -webkit-clip-path: polygon(0 70%, 100% 70%, 100% 80%, 0 80%);
      clip-path: polygon(0 70%, 100% 70%, 100% 80%, 0 80%);
  }
  21.9% {
      opacity: 1;
      transform: translate3d(var(--gap-horizontal), 0, 0);
  }
  22%, 100% {
      opacity: 0;
      transform: translate3d(0, 0, 0);
      -webkit-clip-path: polygon(0 0, 0 0, 0 0, 0 0);
      clip-path: polygon(0 0, 0 0, 0 0, 0 0);
  }
}

